<template>
  <div class="page" id="news-list">
    <van-pull-refresh class="content" v-model="isLoading" @refresh="refreshData(0)">
      <van-list
              v-model="isLoading"
              :finished="finished"
              finished-text="没有更多了"
              @load="refreshData(1)"
      >
        <div class="list-item bgc-white p15 px-b" v-for="news in newsList" :key="news.id"
             @click="toNewsDetail(news.id)">
          <div class="list-item-left mr5">
            <div class="text-overflow-line2">{{ news.title }}</div>
            <p class="f12 gray">{{ news.publish }}</p>
          </div>
          <img v-lazy="news.pic.op_spic" width="120" height="70" alt="">
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'newsList',
    data() {
      return {
        isLoading: false,
        newsList: [],
        finished: false,
        page: 1,
        pageSize: 20
      }
    },
    methods: {
      refreshData(type) {
        if (type == 0) {
          this.page = 1
        }
        let zone_id = utils.getQueryString(window.location.href, 'zone_id') || 1
        this.axios.get(this.apiConfig.flashList, {
          params: {
            zone_id,
            page: this.page,
            pageSize: this.pageSize
          }
        }).then(res => {
          if (res.s === 1) {
            this.isLoading = false
            this.$store.dispatch('UPDATE_LOADINGSTATE', false)
            if (type == 1) {
              this.page++
              this.newsList = this.newsList.concat(res.d.data)
              if (this.newsList.length >= res.d.total) {
                this.finished = true
              }
            } else {
              this.newsList = res.d.data
            }
          }
        })
      },
      toNewsDetail(id) {
        this.$router.push({
          name: 'newsDetail',
          query: {id}
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .list-item, .list-item-left {
    display: flex;
    justify-content: space-between;
  }

  .list-item-left {
    flex-direction: column;
  }
</style>
